.Button {
    
    font-size: 0.8rem;
    align-self: center;
    border-radius: 10px;
    border-color: transparent;
    padding: 4px 10px;
    margin-left: 2px;
    margin-right: 2px;
    min-width: 75px;
}

.Button:hover {
    background-color: black;
    opacity: 0.8;
    border-color: transparent;
    cursor: pointer;
}


.Wide {
    padding: 10px 50px;
    width: 100%;
}


.centerButton {
    display: flex;
    justify-content: center;
}